<template>
  <div class="row dashboard-info-widgets">

    <div class="col-md-4 col-xl-4">
      <vuestic-widget class="info-widget brand-danger">
        <div class="info-widget-inner">
          <div class="stats">
            <div class="stats-number">
              <i class="ion ion-speedometer stats-icon icon-wide"></i>
              {{this.json.consumption}}
            </div>
            <div class="stats-title">Total Consumption</div>
          </div>
        </div>
      </vuestic-widget>
      </div>

      <div class="col-md-4 col-xl-4">
        <vuestic-widget class="info-widget brand-info">
          <div class="info-widget-inner">
            <div class="stats">
              <div class="stats-number">
                <i class="ion ion-flash  stats-icon icon-wide "></i>
                {{this.json.power}}
              </div>
              <div class="stats-title">Total Power</div>
            </div>
          </div>
        </vuestic-widget>
        </div>

        <div class="col-md-4 col-xl-4">
          <vuestic-widget class="info-widget brand-info">
            <div class="info-widget-inner">
              <div class="stats">
                <div class="stats-number">
                  <i class="ion ion-outlet stats-icon icon-wide"></i>
                  {{this.json.plugnumber}}
                </div>
                <div class="stats-title">Online Plugs</div>
              </div>
            </div>
          </vuestic-widget>
        </div>
    </div>
</template>

<script>
import ProgressBar from '../../components/vuestic-components/vuestic-progress-bar/VuesticProgressBar'

export default {
  name: 'dashboard-info-widgets',
  components: {
    ProgressBar
  },
  data: function() {
    return {
      json: null
    }
  },
  mounted() {
    this.getjson()
  },
  methods: {
    getjson: function() {
      this.$http.get('http://localhost/json/infoweight.php').then(
        function(res) {
          // 处理成功的结果
          this.json = res.data
          console.log(this.json)
        }, function(res) {
          console.log('fail json')
        }
      )
    }

  }
}
</script>

<style lang="scss" scoped>
@import "../../sass/_variables.scss";

.stats-number,
.stats-title {
  line-height: 1;
}

.info-widget-inner {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;

  &.has-chart {
    justify-content: space-between;
  }

  .stats {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
}

.stats-number {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 2.625rem;
  margin-bottom: 0.5rem;

  .stats-icon {
    font-size: 1.5625rem;
    position: absolute;
    top: 0.625rem;
    left: -1.25rem;

    &.icon-wide {
      left: -1.875rem;
    }
  }
}
</style>



